export default {
	data() {
		return {
			isFocus: false,
			scrollHeight: 'auto', // 内容区域高度
			scrollViewId: '', // 滚动到最底部
			voicePlayingId: '', // 正在播放的消息ID
			content: '',
			list:[],
			chatToken:"",
			userInfo:uni.getStorageSync("userInfo")
		}
	},
	async onLoad(option) {
		const chatToken = option.chatToken;
		this.chatToken = chatToken;
		// 初始化内容高度
		this.setScrollHeight();

		this.$bus.$on("sendMsg",this.popMessage)
	},
	onUnload(){
		this.$bus.$off("sendMsg",this.popMessage)
	},
	methods: {
		popMessage(msgInfo){
			this.list.push({
				source:"friend",
				msg:msgInfo.msg,
				avatarUrl:"/static/images/pyy.png",
				nickName:"深圳彭于晏"
			})
		},
		async send(){
			const content = this.content;
			const result = await this.$API.sendMsg({
				msg:content,
				chatToken: this.chatToken
			})
			this.content="";
			if(result.code ===200){
				this.list.push({
				source:"mine",
				msg:content,
				avatarUrl:this.userInfo.avatarUrl,
				nickName:this.userInfo.nickName
			})
			}
		},
		// 初始化滚动
		initScrollBar() {
			setTimeout(() => {
				this.scrollViewId = ''
				setTimeout(() => {
					this.scrollViewId = 'bottom'
					setTimeout(() => {
						this.scrollViewId = ''
					}, 100)
				}, 100)
			}, 100)
		},
		// 监听输入聚焦
		inputFocus(e) {
			this.setScrollHeight(e.detail.height)
			this.initScrollBar()

			uni.onKeyboardHeightChange(res => {
				this.setScrollHeight(res.height)
				this.initScrollBar()
			})
		},
		// 设置scroll的高度
		setScrollHeight(descHeight = 0) {
			this.scrollHeight = `calc(100vh - 110rpx - ${descHeight}px)`
		}
	},
}
